}

.fl-form .fl-is-active label.fl-label {
  opacity: 1;
  visibility: visible;
  background-color: transparent;
  left: 10px;
  top: -9px;
  padding: 3px
}

.fl-form .fl-is-active label.fl-label::before {
  content: "";
  display: block;
  position: absolute;
  top: 8px;
  right: 0;
  left: 0;
  height: 4px;
  background-color: #fff;
  z-index: -1
