.tr-breadcurmb-area {
  position: relative;
  background-size: cover;
  background-position: center;
  padding: 100px 0;
  color: #fff !important;
}

.tr-breadcurmb-title {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 10px;
}

.tr-breadcurmb-list span {
  margin: 0 5px;
}

.tr-about-area {
  background-color: #fff;
  padding-top: 120px;
  padding-bottom: 105px;
}

.locationdiv iframe {
  width: 100%;
  height: 400px;
  border: none;
}

.bgcolor2 {
  background-color: #f87a1f;
}

.circleimg img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: white;
  padding: 10px;
}

.txtRight {
  text-align: right;
}

.z-index-1 {
  z-index: 1;
  position: relative;
}

.z-index-2 {
  z-index: 2;
  position: relative;
}

@media (max-width: 768px) {
  .txtRight {
    text-align: center;
    margin-top: 20px;
  }

  .circleimg img {
    margin: auto;
  }
}
